.BuildControls {
    width: 100%;
    background-color: #f3d4a5;
    display: flex;
    flex-flow: column;
    align-items: center;
    box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0;
}

.OrderButton {
    background-color: #f7a800;
    outline: none;
    cursor: pointer;
    border: 2px solid #966909;
    color: #966909;
    font-family: inherit;
    font-size: 1.2em;
    font-weight: bold;
    margin: 10px 0 0 5px;
    padding: 15px 93px;
}

.ResetButton {
    background-color: #f70800;
    border: none;
    outline: none;
    cursor: pointer;    
    color: white;
    font-family: inherit;
    font-size: 1.2em;
    font-weight: bold;
    margin: 10px 0 0 5px;
    padding: 15px 95px;
}

.ResetButton:hover {
    background-color: #fd386a;
}

.OrderButton:hover, .OrderButton:active {
    background-color: #ffd06b;
    border: 2px solid #966909;
    color: #966909;
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 2px solid #ccc;
    color: #888888;
}

.OrderButton:not(:disabled) {
    animation: enable 0.3s linear;
}

@keyframes enable {
    0% {
        transform: scale(1);
    }
    60% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}